/* COLLAPSE CONTAINER */
.collapse-container {
	position: relative;
	height: 0px;
	margin: 0 0 50px 0;
}

.collapse-container.active {
	height: 100%;
}

/* BTN */
.clickable {
	cursor: pointer;
	border: 1px solid #265d63;
	background-color: #FFF;
	font-size: 15px;
	color: #34b8c8;
	width: 150px;
	text-align: center;
	padding: 20px;
	border-radius: 45px;
	margin: 50px 0 0 10px;
	line-height: 30px;
	font-weight: bold;
	z-index: 10;
}
.clickable:hover {
	border: 1px solid #34b8c8;
	background-color: #265d63;
	color: #FFF;
	-webkit-transition: background-color 1s ease-out, border 1s ease-out;
	-o-transition: background-color 1s ease-out, border 1s ease-out;
	transition: background-color 1s ease-out, border 1s ease-out;
}
.clickable.active {
	-webkit-animation-name: clickableIn;
	        animation-name: clickableIn;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
  	-webkit-animation-fill-mode: forwards;
  	        animation-fill-mode: forwards;
}
.clickable.unactive {
	-webkit-animation-name: clickableOut;
	        animation-name: clickableOut;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
  	-webkit-animation-fill-mode: forwards;
  	        animation-fill-mode: forwards;
  	opacity: 0;
  	cursor: default;
}
@-webkit-keyframes clickableOut {
	0%{
	    display: inline;
	    height: auto;
	    opacity: 1;
	    -webkit-transform: translateX(0px);
	            transform: translateX(0px);
	}
	50%{
	    opacity: 0;
	    -webkit-transform: translateX(1000px);
	            transform: translateX(1000px);
	}
	100%{
	    display: none;
		border: none;
	}
}
@keyframes clickableOut {
	0%{
	    display: inline;
	    height: auto;
	    opacity: 1;
	    -webkit-transform: translateX(0px);
	            transform: translateX(0px);
	}
	50%{
	    opacity: 0;
	    -webkit-transform: translateX(1000px);
	            transform: translateX(1000px);
	}
	100%{
	    display: none;
		border: none;
	}
}
@-webkit-keyframes clickableIn {
	0%{
	    display: none;
		border: none;
	    -webkit-transform: translateX(1000px);
	            transform: translateX(1000px);
	}
	50%{
	    opacity: 0.5;
	}
	100%{
	    display: inline;
	    height: auto;
	    opacity: 1;
	    -webkit-transform: translateX(0px);
	            transform: translateX(0px);
	}
}
@keyframes clickableIn {
	0%{
	    display: none;
		border: none;
	    -webkit-transform: translateX(1000px);
	            transform: translateX(1000px);
	}
	50%{
	    opacity: 0.5;
	}
	100%{
	    display: inline;
	    height: auto;
	    opacity: 1;
	    -webkit-transform: translateX(0px);
	            transform: translateX(0px);
	}
}

/* CROSS BTN STYLE ACIVE */
.collapse-btn .btn-line {
  width: 40px;
  height: 2px;
  background-color: #34b8c8;
  display: block;
  margin: 11px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  z-index: 0;
}

.collapse-btn.is-active:hover,
.collapse-btn.is-active:focus {
  cursor: pointer;
}

/* CROSS BTN STYLE */

.collapse-btn-cross {
  opacity: 0;
  -webkit-transform: translateX(-1000px);
      -ms-transform: translateX(-1000px);
          transform: translateX(-1000px);
  position: absolute;
  right: 50px;
  top: 0px;
  -webkit-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

.collapse-btn-cross.is-active{
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  opacity: 1;
}

.collapse-btn-cross:before{
  content: "";
  position: absolute;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  border: 2px solid transparent;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
  border-radius: 100%;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.collapse-btn-cross.is-active:before{
  border: 1px solid #ecf0f1;
}

.collapse-btn-cross.is-active .btn-line{
  width: 25px;
}

.collapse-btn-cross.is-active .btn-line:nth-child(2){
  opacity: 0;
}

.collapse-btn-cross.is-active .btn-line:nth-child(1){
  -webkit-transform: translateY(13px);
  -ms-transform: translateY(13px);
  transform: translateY(13px);
}

.collapse-btn-cross.is-active .btn-line:nth-child(3){
  -webkit-transform: translateY(-13px) rotate(90deg);
  -ms-transform: translateY(-13px) rotate(90deg);
  transform: translateY(-13px) rotate(90deg);
}

/* MOTION CONTENT */
.collapse-hide {
	background-color: #FFF;
	-webkit-transform: scale(0, 0);
	    -ms-transform: scale(0, 0);
	        transform: scale(0, 0);
    -webkit-transform-origin: 0 50%;
        -ms-transform-origin: 0 50%;
            transform-origin: 0 50%;
    -webkit-transition: -webkit-transform 1s ease;
    transition: -webkit-transform 1s ease;
    -o-transition: transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
	z-index: 20;
    height: 0px;
}
.collapse-show {
    -webkit-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
            transform: scale(1, 1);
    height: 100%;
    -webkit-transition: -webkit-transform 1s ease;
    transition: -webkit-transform 1s ease;
    -o-transition: transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
}

/* CROSS BROWSER COMPATIBILITY */
@media screen and (min-width: 801px) {
	.clickable {
		font-size: 20px;
		width: 400px;
		margin: 100px 0 0 50px;
	}
}




