.slider-gallery {
	margin: 0 auto;
	width: 100vw;
	text-align: center;
	margin: 150px 0 150px 0;
}

.slider-gallery > p {
	font-size: 40px;
	padding: 0 0 40px 0;
}
.slider-gallery > div > img {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
	-webkit-transition: -webkit-filter 1s ease-in;
	transition: -webkit-filter 1s ease-in;
	-o-transition: filter 1s ease-in;
	transition: filter 1s ease-in;
	transition: filter 1s ease-in, -webkit-filter 1s ease-in;
	-webkit-transform: scale(1);
	    -ms-transform: scale(1);
	        transform: scale(1);
	-webkit-transition: -webkit-transform 200ms ease-out;
	transition: -webkit-transform 200ms ease-out;
	-o-transition: transform 200ms ease-out;
	transition: transform 200ms ease-out;
	transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
}

.slider-gallery > div > img:hover {
	-webkit-filter: grayscale(0%);
	        filter: grayscale(0%);
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}


@media screen and (min-width: 800px) {
	.slider-gallery > div  {
		width: 80vw;
		text-align: center;
		margin: 0 auto;
	}
}


