.infographie-home {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 5vh;
}

@media screen and (max-width: 600px) {
	.info-img-lg {
		display: none;
	}
	.info-img-sm {
		height: 65vh;
		display: inline;	
	}
}

@media screen and (min-width: 601px) and (max-width: 1200px) {
	.info-img-lg {
		height: 47vh;
		display: inline;
		margin: 0 0 7vh 0;
	}
	.info-img-sm {
		display: none;	
	}
}

@media screen and (min-width: 1201px) {
	.info-img-lg {
		height: 70vh;
		display: inline;
	}
	.info-img-sm {
		display: none;	
	}
}

.section-slider {
    z-index: 0;
    width: 100%;
    height: 100vh;
    margin: 0 0 40px 0;
    position: relative;
}

.intro {
	top: 45vh;
	position: relative;
}
.intro > .motion > p {
	position: relative;
	top: 20vh;
	left: 50%;
	width: 200px;
	margin-left: -100px;
	text-align: center;
	color: #FFF;
	text-shadow: 1px 1px 2px #265d63;
    z-index: 30;
}

.skills {
	background-color: #FFF;
	width: 100%;
	display: inline-block;
	text-align: center;
	padding: 250px 0 50px 0;
	color: #34B8C8;
	z-index: 20;
}

.skills p {
	margin: 100px 10% 0 10%;
	font-size: 20px;
	color: #34B8C8;
}

@media screen and (min-width: 401px) {
	.skills p {
		padding: 0 10vw;
		font-size: 30px;
		line-height: 50px;
	}
}
.skills h2 {
	padding: 0 10% 100px 10%;
	line-height: 60px;
	text-align: center;
	font-size: 40px;
	color: #34B8C8;
	letter-spacing: 6px;
}

/* PARALLAX AND MOTION */
.slider-motion {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
@media only screen and (max-width: 824px) {
    .slider-motion {
    	width: 100%;
        height: 95%;
    	z-index: 0;
    }
}

@media only screen and (min-width: 825px) {
    .slider-motion {
    	width: 100%;
        height: 106vh;
        z-index: 0;
    }
}


